.project-body {
    display: flex;
    align-items: center;
}

.project-img {
    width: 260px;
    margin: 5px;
    border-radius: 25%;
    padding: 30px;
}

.project-about {
    border-left: solid #3f4666;
    padding: 30px;
    text-align: justify;
}

.project-about .project-title {
    margin-bottom: 10px;
}

.project-about .project-title .title {
    font-weight: bold;
    font-size: larger;
}

.project-about .project-title .stack {
    font-style: italic;
}

@media screen and (max-width: 768px) {
    .project-body {
        flex-direction: column;
    }

    .project-about {
        border-left: none;
        border-top: solid #3f4666;
        padding: 30px;
        text-align: justify;
    }
}