.hero-area {
    display: flex;
}

.hero-image {
    background-image: url('../assets/home.png');
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-attachment: scroll;
    background-repeat: no-repeat;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
}

.hero-content {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    min-height: 100vh;
    min-width: 98vw;
    color: #ffde59;
    padding-bottom: 0px;
}

.hero-content div h1 {
    font-size: 3.9em;
    font-family: "Lucida Handwriting", cursive;
    font-weight: 200;
    margin-top: 0px;
    margin-bottom: 0px;
}

.hero-content h3 {
    font-family: 'Courier New', Courier, monospace;
    font-weight: 100;
    margin-top: 0px;
}

.hero-content .spacer {
    height: 0.2vh;
}