.about-topic {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 0px;
}

.whoami {
    font-family: 'Courier New', Courier, monospace;
}

.about-body {
    display: flex;
    align-items: center;
}

.bio {
    border-left: solid #3f4666;
    padding: 30px;
    text-align: justify;
}

.picture {
    width: 30%;
    margin: 5px;
    border-radius: 50%;
    padding: 30px;
}

.socials-tab {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    min-width: 40px;
    min-height: 100%;
    
}

.socials-tab .icon {
    font-size: 40px;
    margin-top: 15px;
    margin-bottom: 15px;
    color: #3f4666;
}

.resume-area {
    display: flex;
    align-content: center;
    justify-content: center;
}

.button {
    align-items: center;
    margin: 30px;
    color: #3f4666;
    cursor: pointer;
    border-radius: 25%;
    border: solid #3f4666;
    padding-left: 15px;
    padding-right: 15px;
}

@media screen and (max-width: 768px) {
    .about-body {
        flex-direction: column;
    }

    .picture {
        width: 80%;
    }

    .bio {
        border-top: solid #3f4666;
        border-left: none;
        padding: 30px;
        text-align: justify;
    }

    .socials-tab {
        flex-direction: row;
    }
}