.nav-bar {
    z-index: 1;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    justify-content: space-around;
    border-radius: 0;
    border: 0;
    list-style-type: none;
    background-color: #3f4666;
    position: fixed;
    right: 0;
    left: 0;
    box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-bar .nav-item-g {
    font-size: medium;
    font-weight: bold;
    padding-top: 15px;
    padding-bottom: 15px;
    color: #ffde59;
    flex-grow: 1;
    cursor: pointer;
    text-align: center;
}

.nav-bar .nav-item {
    font-size: medium;
    font-weight: bold;
    padding-top: 15px;
    padding-bottom: 15px;
    color: #ffde59;
    flex-grow: 1;
    cursor: pointer;
    text-align: center;
    
}

a {
    text-decoration: none;
}

.nav-bar .nav-item-g:hover {
    background-color: #51597e;
}

.nav-bar .nav-item:hover {
    background-color: #51597e;
}

.nav-bar button {
    font-family: inherit;
    border: none;
    background-color: transparent;
}

.mobile {
    display: none;
}

.mobile:hover {
    background-color: #51597e;
}

@media screen and (max-width: 768px) {
    .nav-bar .nav-item{
        display: none;
    }

    .nav-bar a {
        text-align: left;
    }

    .mobile {
        display: block;
        padding-right: 25px;
        padding-left: 25px;
        color: #ffde59;
    }

    #spacer {
        flex-grow: 5;
    }

    #nav-links {
        flex-direction: column;
    }
}